.QuizFinish {
  padding-top: 40px;

  .marquee-container {
    border-top: 2px solid #000;
    border-bottom: 2px solid #000;
    padding: 21px - 25px;
    background-color: #efff36;

    .child {
      display: flex;
      align-items: center;
      font-family: "Grtsk Zetta";
      font-size: 32px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: -0.03em;

      &::after {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 6px;
        height: 6px;
        margin-left: 24px;
        margin-right: 35px;
        background-color: #000;
        border-radius: 50%;
      }
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  &__content {
    padding-top: 80px;
    padding-left: 40px;
  }

  &__title {
    max-width: 630px;
    margin-bottom: 40px;
    font-size: 80px;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__subtitle,
  &__text {
    max-width: 630px;
    font-family: "Grtsk Zetta";
    font-size: 24px;
    font-weight: 300;
  }

  &__subtitle {
    margin-bottom: 20px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 100%;
    max-width: 460px;
    height: 130px;
    background: center / contain no-repeat url("../../images/star_green.svg");
    border: none;
    appearance: none;
    outline: none;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    pointer-events: all;

    &-wrapper {
      position: absolute;
      bottom: 15%;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      max-width: 460px;
    }

    &-tip {
      position: absolute;
      z-index: -1;
      right: -100px;
      top: -75px;
      transform: rotate(15deg);
      width: 100%;
      padding: 35px 24px;
      max-width: 480px;
      height: 110px;
      font-family: "Grtsk Zetta";
      font-size: 21px;
      font-weight: 300;
      text-align: center;
      color: #fff;
      background: center / contain no-repeat url("../../images/btn_tip.svg");
    }
  }
}
