.QuizCard {
  height: 100%;
  padding-top: 70px;

  .container {
    height: 100%;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }

  &__text {
    width: 100%;
    max-width: 400px;
  }

  &__title {
    margin-bottom: 34px;
    font-family: "Grtsk Giga";
    font-size: 54px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -0.03em;
  }

  &__tip {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    text-transform: uppercase;

    &:before {
      content: "";
      width: 12px;
      height: 12px;
      display: inline-block;
      vertical-align: middle;
      background: url("../../images/star2.svg");
    }
  }

  &__options {
    display: flex;
    width: 100%;
    max-width: 678px;
    max-height: 720px;
    gap: 10px;
  }

  &__option {
    flex: 1 0 calc(50% - 10px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 40px;
    background-color: #dddddd;
    font-family: "Grtsk Zetta";
    font-size: 32px;
    font-weight: 700;
    text-align: center;
    letter-spacing: -0.03em;
    text-transform: uppercase;
    cursor: pointer;
  }
}
