.Hero__title {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 12;
  pointer-events: none;

  &.animate {
    animation: opacity ease 2s 1 forwards 3s;
  }

  &-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &-title {
    margin-bottom: 20px;
    font-size: 60px;
    font-weight: 700;
    letter-spacing: -0.03em;
    text-transform: uppercase;
    text-align: center;
  }

  &-text {
    margin-bottom: 48px;
    font-family: "Grtsk Zetta";
    font-size: 18px;
    font-weight: 300;
    text-transform: uppercase;
  }

  &-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 100%;
    max-width: 460px;
    height: 130px;
    background: center / contain no-repeat url("../../images/star_green.svg");
    border: none;
    appearance: none;
    outline: none;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    pointer-events: all;
  }
}

@keyframes opacity {
  0% {
    height: 100%;
    opacity: 1;
  }
  100% {
    height: 0;
    opacity: 0;
  }
}
