.QuizAnswer {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 99;
  padding: 40px;
  opacity: 0;
  background: center / cover no-repeat url("../../images/background.png") #fff;
  transition: opacity 0.5s ease-in-out;

  &.visible-enter-done {
    opacity: 1;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 70px;
    padding: 80px 50px;
    max-height: 720px;
    background-color: #8bf62a;
  }

  &__title {
    max-width: 600px;
    margin: 0 auto;
    font-family: "Grtsk Zetta";
    font-size: 69px;
    font-weight: 700;
    color: #000;
    letter-spacing: -0.03em;
    text-align: center;
    text-transform: uppercase;
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__animation {
    width: 100%;
    max-width: 320px;
    position: relative;

    > div {
      position: absolute;
      left: -20%;
      top: -350px;
      width: 100vw;
      z-index: 99;
      pointer-events: none;
    }
  }

  &__next {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: -60px;
    right: 0;
    padding: 0;
    width: 100%;
    max-width: 460px;
    height: 130px;
    background: center / contain no-repeat url("../../images/star_blue.svg");
    border: none;
    appearance: none;
    color: #fff;
    outline: none;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    pointer-events: all;
  }

  &__tip {
    position: relative;
    width: 100%;
    max-width: 700px;
    transform: rotate(-6deg);
    z-index: 1;

    &-wrapper {
      background-color: #fff;
      padding: 50px;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      right: -5px;
      bottom: -5px;
      width: 100%;
      height: 100%;
      background-color: #000;
      z-index: -1;
    }

    h3 {
      margin-bottom: 10px;
      text-transform: uppercase;
      font-size: 18px;
      font-weight: 400;
    }

    p {
      font-family: "Grtsk Zetta";
      font-size: 32px;
      letter-spacing: -0.003em;
      font-weight: 300;
    }
  }
}
