.draggable-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  &.animate {
    pointer-events: none;
  }
}

.draggable-item {
  &.animate {
    transition: top 0.9s ease-in-out, transform 0.9s ease-in-out;
    animation: 2s rotateAndMove forwards ease-in-out 1.5s;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

@keyframes rotateAndMove {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
