.header {
  padding-top: 40px;
  position: relative;
  z-index: 10;

  &__wrapper {
    display: flex;
    gap: 20px;
  }

  &__logo {
    display: flex;
    align-items: center;
    width: 244px;
    height: 60px;
    padding-left: 30px;
    position: relative;
    background: #fff;
    border: 1px solid #dddddd;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.09);
  }

  &__avatar {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
  }

  &__stage {
    display: flex;
    align-items: center;
    height: 60px;
    padding: 0 32px;
    border-radius: 5px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    background-color: #6018f2;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    &.animate {
      display: none;
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 6px 20px 6px 6px;
      gap: 10px;
      height: 60px;
      border: 1px solid #000000;
      border-radius: 5px;
      background-color: #fff;

      .currentQuestion {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        font-size: 16px;
        font-weight: 700;
        color: #fff;
        letter-spacing: -0.03em;
        background-color: #6018f2;
      }

      .countQuestion {
        font-size: 16px;
        font-weight: 300;
        letter-spacing: -0.03em;

        span {
          font-weight: 700;
        }
      }
    }
  }
}
